html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Spartan", sans-serif;
  font-weight: 500;
  overflow-x: hidden;
}
body.nav-open-noscroll {
  height: 100vh;
  overflow: hidden;
}

button {
  font-family: inherit;
  border: none;
  padding: 0;
  cursor: pointer;
  outline: none;
}

.flex-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hamburger {
  position: absolute;
  z-index: 2;
  width: 100%;
}
.hamburger .hamburger-content {
  position: relative;
  align-items: center;
  min-height: 10em;
  display: flex;
  padding: 0 4.5em;
  z-index: 1000;
}
@media (max-width: 40em) {
  .hamburger .hamburger-content {
    min-height: 7em;
    padding: 0 1.5em;
  }
}
.hamburger .hamburger-content .hamburger-btn {
  display: flex;
  background-color: transparent;
}
.hamburger .hamburger-content .hamburger-btn svg {
  width: 20px;
  height: auto;
}
@media (min-width: 40em) {
  .hamburger .hamburger-content .hamburger-btn {
    display: none;
  }
}
.hamburger .hamburger-content .header-logo svg {
  display: block;
}
@media (max-width: 40em) {
  .hamburger .hamburger-content .header-logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}
.hamburger .hamburger-content nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4.5em;
}
.hamburger .hamburger-content nav .menu-list {
  display: flex;
  padding: 0;
  list-style: none;
}
.hamburger .hamburger-content nav .menu-list .menu-item a {
  position: relative;
  text-decoration: none;
  font-size: 0.8rem;
  color: white;
}
@media (max-width: 40em) {
  .hamburger .hamburger-content nav .menu-list .menu-item a {
    color: black;
    font-weight: 700;
  }
}
.hamburger .hamburger-content nav .menu-list .menu-item a::after {
  content: "";
  width: 0;
  height: 1px;
  background-color: white;
  position: absolute;
  left: 50%;
  bottom: -12px;
  transform: translateX(-50%);
  transition: all 0.4s ease;
}
@media (hover: hover) and (pointer: fine) {
  .hamburger .hamburger-content nav .menu-list .menu-item a:hover:after {
    width: 70%;
  }
}
.hamburger .hamburger-content nav .menu-list .menu-item:not(:last-child) {
  padding-right: 2.5em;
}
@media (max-width: 40em) {
  .hamburger .hamburger-content nav .menu-list .menu-item:not(:last-child) {
    padding-right: 2em;
  }
}
@media (max-width: 23em) {
  .hamburger .hamburger-content nav .menu-list .menu-item:not(:last-child) {
    padding-right: 1em;
  }
}
@media (max-width: 40em) {
  .hamburger .hamburger-content nav.open {
    display: inline-flex;
  }
}
@media (max-width: 40em) {
  .hamburger .hamburger-content nav {
    background-color: white;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 1.5em;
    display: none;
  }
}
@media (max-width: 40em) {
  .hamburger .hamburger-overlay {
    background: #090909;
    opacity: 0.5;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 999;
    display: none;
  }
  .hamburger .hamburger-overlay.overlay_visible {
    display: block;
  }
}

.main-wrapper {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(3, 1fr);
  min-height: 100vh;
}
@media (max-width: 64em) {
  .main-wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media (max-width: 40em) {
  .main-wrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 350px auto 235px auto 220px;
  }
}
.main-wrapper .slide-hero-bg {
  position: relative;
  grid-column: 1/7;
  grid-row: 1/3;
}
@media (max-width: 64em) {
  .main-wrapper .slide-hero-bg {
    grid-column: 1/3;
    grid-row: 1/2;
  }
}
@media (max-width: 40em) {
  .main-wrapper .slide-hero-bg {
    grid-column: 1;
    grid-row: 1/2;
  }
}
.main-wrapper .slide-hero-bg .hero-bg {
  width: 100%;
  height: 100%;
  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: all 0.4s ease;
}
.main-wrapper .slide-hero-bg .hero_1 {
  background-image: url("../../../assets/images/desktop-image-hero-1.jpg");
}
@media (max-width: 26em) {
  .main-wrapper .slide-hero-bg .hero_1 {
    background-image: url("../../../assets/images/mobile-image-hero-1.jpg");
  }
}
.main-wrapper .slide-hero-bg .hero_2 {
  background-image: url("../../../assets/images/desktop-image-hero-2.jpg");
}
@media (max-width: 26em) {
  .main-wrapper .slide-hero-bg .hero_2 {
    background-image: url("../../../assets/images/mobile-image-hero-2.jpg");
  }
}
.main-wrapper .slide-hero-bg .hero_3 {
  background-image: url("../../../assets/images/desktop-image-hero-3.jpg");
}
@media (max-width: 26em) {
  .main-wrapper .slide-hero-bg .hero_3 {
    background-image: url("../../../assets/images/mobile-image-hero-3.jpg");
  }
}
.main-wrapper .slide-hero-bg .btn-wrapper {
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
  transform: translateX(100%);
  width: 10vw;
  height: 5vw;
  min-height: 55px;
  min-width: 110px;
  background-color: black;
}
@media (max-width: 64em) {
  .main-wrapper .slide-hero-bg .btn-wrapper {
    right: 0;
    transform: translateX(0);
  }
}
.main-wrapper .slide-hero-bg .btn-wrapper button {
  width: 50%;
  height: 100%;
  background-color: black;
  transition: all 0.4s ease;
}
@media (hover: hover) and (pointer: fine) {
  .main-wrapper .slide-hero-bg .btn-wrapper button:hover {
    background-color: #454545;
  }
}
@media (max-width: 40em) {
  .main-wrapper .slide-hero-bg .btn-wrapper button svg {
    transform: scale(0.8);
  }
}
.main-wrapper .slide-hero-content {
  grid-column: 7/11;
  grid-row: 1/3;
  padding: 5em 5.5em;
}
@media (max-width: 64em) {
  .main-wrapper .slide-hero-content {
    grid-column: 1/2;
    grid-row: 2/3;
    padding: 5em 2em;
  }
}
@media (max-width: 40em) {
  .main-wrapper .slide-hero-content {
    grid-column: 1;
    grid-row: 2/3;
    padding: 2.5em 1.5em;
  }
}
.main-wrapper .slide-hero-content .slides {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-wrapper .slide-hero-content .slides li {
  display: none;
}
.main-wrapper .slide-hero-content .slides li.active-item {
  display: list-item;
}
.main-wrapper .slide-hero-content .slides .hero-heading {
  font-size: clamp(1.8rem, 5vw, 2.3rem);
  font-weight: 600;
  line-height: 1.1;
  overflow-wrap: break-word;
}
.main-wrapper .slide-hero-content .slides .hero-text {
  color: #a1a1a1;
  line-height: 1.6;
  font-size: 0.8rem;
}
.main-wrapper .slide-hero-content .shop-link {
  color: black;
  font-size: 0.8rem;
  font-weight: 600;
  letter-spacing: 8px;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  width: fit-content;
  margin: 2em 0;
  transition: all 0.4s ease;
}
.main-wrapper .slide-hero-content .shop-link svg {
  transition: all 0.4s ease;
}
@media (hover: hover) and (pointer: fine) {
  .main-wrapper .slide-hero-content .shop-link:hover {
    color: #a1a1a1;
  }
  .main-wrapper .slide-hero-content .shop-link:hover svg {
    transform: translateX(20px);
  }
  .main-wrapper .slide-hero-content .shop-link:hover svg path {
    fill: #a1a1a1;
  }
}
.main-wrapper .about-left-image {
  grid-column: 1/4;
  grid-row: 3/4;
  background: white url("../../../assets/images/image-about-dark.jpg") no-repeat center;
  background-size: cover;
}
@media (max-width: 64em) {
  .main-wrapper .about-left-image {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
@media (max-width: 40em) {
  .main-wrapper .about-left-image {
    grid-column: 1;
    grid-row: 3/4;
  }
}
.main-wrapper .about-content {
  grid-column: 4/8;
  grid-row: 3/4;
  padding: 0 3em;
}
@media (max-width: 64em) {
  .main-wrapper .about-content {
    grid-column: 2/3;
    grid-row: 3/4;
    padding: 5em 2em;
  }
}
@media (max-width: 40em) {
  .main-wrapper .about-content {
    grid-column: 1;
    grid-row: 4/5;
    padding: 2.5em 1.5em;
  }
}
.main-wrapper .about-content .about-heading {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.main-wrapper .about-content .about-text {
  font-size: 0.8rem;
  color: #a1a1a1;
  line-height: 1.6;
}
.main-wrapper .about-right-image {
  grid-column: 8/11;
  grid-row: 3/4;
  background: white url("../../../assets/images/image-about-light.jpg") no-repeat center;
  background-size: cover;
}
@media (max-width: 64em) {
  .main-wrapper .about-right-image {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}
@media (max-width: 40em) {
  .main-wrapper .about-right-image {
    grid-column: 1;
    grid-row: 5/6;
  }
}